@font-face {
  font-weight: 400;
  font-family: "CenturyGothic";
  font-style: normal;
  font-display: swap;
  src: url("../fonts/CenturyGothic.woff2") format("woff2"), url("../fonts/CenturyGothic.woff") format("woff"); }

@font-face {
  font-weight: 700;
  font-family: "CenturyGothic";
  font-style: normal;
  font-display: swap;
  src: url("../fonts/CenturyGothic-Bold.woff2") format("woff2"), url("../fonts/CenturyGothic-Bold.woff") format("woff"); }

body {
  font-family: "CenturyGothic", "Roboto", sans-serif;
  font-size: 14px;
  line-height: 1.6em;
  font-weight: 300; }

.visually-hidden:not(:focus):not(:active),
input[type="checkbox"].visually-hidden,
input[type="radio"].visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip: rect(0 0 0 0);
  overflow: hidden; }

.page-header {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-template-rows: 72px 40px;
  align-items: center; }

.page-header__logo {
  grid-column: 1/2;
  grid-row: 1/2; }

.page-header__open-btn {
  position: relative;
  grid-column: 2/3;
  grid-row: 1/2;
  justify-self: end;
  width: 18px;
  height: 3px;
  margin-right: 15px;
  border: none;
  border-radius: 2px;
  background-color: #3b3d42; }
  .page-header__open-btn::after, .page-header__open-btn::before {
    position: absolute;
    left: 0;
    width: 18px;
    height: 3px;
    content: "";
    border-radius: 2px;
    background-color: #3b3d42; }
  .page-header__open-btn::after {
    top: 6px; }
  .page-header__open-btn::before {
    bottom: 6px; }

.page-header__close-btn {
  position: relative;
  width: 18px;
  height: 3px;
  margin-right: 20px;
  margin-bottom: 35px;
  align-self: flex-end;
  border: none;
  border-radius: 2px;
  background-color: #3b3d42;
  transform: rotate(45deg); }
  .page-header__close-btn::before {
    position: absolute;
    left: 0;
    top: 0;
    width: 18px;
    height: 3px;
    content: "";
    border-radius: 2px;
    background-color: #3b3d42;
    transform: rotate(90deg); }

.page-header__nav {
  position: fixed;
  display: flex;
  flex-direction: column;
  height: 100%;
  width: 100%;
  padding-top: 35px;
  background-color: white;
  transition: 0.7s;
  transform: translateX(100%); }
  .page-header__nav--open {
    transform: translateX(0); }

.page-header__nav-list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  flex-direction: column;
  margin-left: 15px;
  font-size: 16px;
  gap: 10px; }

.page-header__nav-link {
  font-size: 16px;
  color: #454545;
  text-decoration: none; }
  .page-header__nav-link--active {
    color: #fb395c; }

.page-header__cart {
  display: flex;
  align-items: center;
  gap: 4px;
  margin-left: 15px;
  grid-column: 1/2;
  grid-row: 2/3;
  text-decoration: none; }

.page-header__cart-qty {
  font-size: 16px;
  color: #fb395c; }

.page-header__search {
  display: flex;
  grid-column: 2/3;
  grid-row: 2/3;
  justify-self: end;
  margin-right: 15px; }

@media (min-width: 768px) {
  .page-header {
    grid-template-columns: 1fr 30px 16px;
    grid-template-rows: auto;
    gap: 20px;
    padding: 30px 10px;
    align-items: center; }
  .page-header__logo {
    grid-column: 1/4;
    grid-row: 1/2;
    justify-self: center; }
  .page-header__open-btn,
  .page-header__close-btn {
    display: none; }
  .page-header__nav {
    position: relative;
    grid-column: 1/2;
    grid-row: 2/3;
    height: initial;
    width: unset;
    padding-top: 0;
    background-color: transparent;
    transform: translateX(0); }
  .page-header__nav-list {
    flex-direction: row;
    margin-left: 15px;
    gap: 33px; }
  .page-header__cart {
    margin-left: 0;
    grid-column: 2/3;
    grid-row: 2/3;
    justify-self: end; }
  .page-header__search {
    grid-column: 3/4;
    grid-row: 2/3;
    margin-right: 0; } }

@media (min-width: 1024px) {
  .page-header {
    width: 1210px;
    margin: auto;
    grid-template-columns: 177px 1fr 30px 16px;
    justify-items: end;
    gap: 20px;
    padding: 10px 10px 25px; }
  .page-header__logo {
    grid-column: 1/2; }
  .page-header__nav {
    grid-column: 2/3;
    grid-row: 1/2;
    margin-right: 20px; }
  .page-header__nav-link:hover,
  .page-header__nav-link:active {
    color: #fb395c; }
  .page-header__cart {
    grid-column: 3/4;
    grid-row: 1/2; }
  .page-header__cart:hover .page-header__cart-icon,
  .page-header__search-icon:hover {
    fill: #fb395c; }
  .page-header__search {
    grid-column: 4/5;
    grid-row: 1/2; } }

.page-footer {
  margin: 0 15px;
  padding: 35px 0;
  border: none;
  border-top: 3px dotted #a3c6da; }

.page-footer__title {
  margin: 0 0 24px;
  color: #383838;
  font-size: 16px;
  line-height: 1em; }
  .page-footer__title--social {
    text-align: center; }

.page-footer__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  flex-direction: column;
  gap: 15px;
  margin-bottom: 25px; }

.page-footer__item-link {
  font-family: "Roboto", Sans-serif;
  font-size: 14px;
  font-weight: 300;
  line-height: 1.6em;
  color: #a4a9ab;
  text-decoration: none; }
  .page-footer__item-link--active {
    color: #fb395c; }
  .page-footer__item-link:hover, .page-footer__item-link:active {
    color: #fb395c; }

.page-footer__contacts-item {
  color: #a4a9ab; }

.page-footer__contacts-link {
  color: #fb395c; }

.page-footer__container--social {
  padding: 25px 0 0;
  border: none;
  border-top: 3px dotted #a3c6da; }

.page-footer__social-list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  gap: 15px;
  justify-content: center; }

.social__link-icon:hover,
.social__link-icon:active {
  fill: #fb395c; }

@media (min-width: 768px) {
  .page-footer {
    display: flex;
    gap: 10px;
    padding: 50px 0 20px; }
  .page-footer__container {
    width: 25%; }
    .page-footer__container--social {
      padding: 0;
      justify-content: left;
      border: none; }
  .page-footer__social-list {
    justify-content: left; }
  .page-footer__title--social {
    text-align: left; } }

@media (min-width: 1024px) {
  .page-footer {
    width: 1210px;
    margin: auto; } }
